.App {
  font-family: Arial, sans-serif;
  margin: 20px;
  line-height: 1.6;
}

header {
  text-align: center;
  margin-bottom: 20px;
}

.profile-pictures {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.profile-img {
  border-radius: 50%;
  margin: 0 10px;
  width: 100px;
  height: 100px;
}

section {
  margin-bottom: 20px;
}

h2 {
  border-bottom: 2px solid #000;
  padding-bottom: 5px;
}

ul {
  list-style-type: none;
  padding: 0;
}

ul li {
  background: #f4f4f4;
  margin: 5px 0;
  padding: 10px;
}

.contact a {
  color: #007bff;
  text-decoration: none;
}

.contact a:hover {
  text-decoration: underline;
}
